






import Vue from 'vue'
export default Vue.extend({
  name: 'clock',
  props: {
    done: { //是否运行
      type: Boolean,
      default: true
    }
  },
  data() {
    return{
      clockInterval: 0, //时钟定时器
      tData: this.$store.state.tData
    }
  },
  watch: {
    '$store.state.tData': function(v) {
      this.tData = v;
    }
  },
  methods: {
    startClock: function() {
      this.stopClock(); //防止计时器重复
      this.$store.commit('ClockRun', true);
      this.runing();
    },
    stopClock: function() {
      this.$store.commit('ClockRun', false);
      clearInterval(this.clockInterval);
    },
    runing: function() {
      let Timing = this.$store.state.Timing;
      if(this.tData.behavior.clock.order == 1) {
        this.clockInterval = setInterval(() => { //正计时
          if(!this.$store.state.clockRun || Timing == this.tData.behavior.clock.endSet) {
            this.stopClock();
            this.$store.commit('masker', true);
          }
          if(Timing >= this.tData.behavior.clock.endSet - this.tData.behavior.clock.remind) { //时间小于15S的时候开始抖动
            (this.$refs['clock'] as HTMLElement).classList.add('ani-time')
          }
          this.$store.commit('Timing', Timing++);
        }, 1000);
      } else {
        this.clockInterval = setInterval(() => { //倒计时
          if(!this.$store.state.clockRun || Timing == 0) {
            this.stopClock();
            this.$store.commit('masker', true);
          }
          if(Timing <= this.tData.behavior.clock.remind) { //时间小于15S的时候开始抖动
            (this.$refs['clock'] as HTMLElement).classList.add('ani-time')
          }
          this.$store.commit('Timing', Timing--);
        }, 1000);
      }
    }
  },
  mounted: function() {
    if(this.done) {
      this.startClock();
    }
  },
  destroyed: function() {
    this.stopClock(); //销毁定时器
  }
})
