






















import Vue from 'vue';
import clock from '../components/clock.vue';
import reply from '../components/reply.vue';
import masker from '../components/masker.vue';

export default Vue.extend({
  components: {clock, reply, masker},
  props: {
    clock: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      tData: this.$store.state.tData,
      $util: Vue.prototype.$util
    }
  },
  watch: {
    '$store.state.tData': function(v) {
      this.tData = v;
      this.$store.commit('Timing', this.tData.behavior.clock.outSet); //初始化计时器
    }
  },
  methods: {
    restart: function() { //重新开始
      if(this.tData.behavior.masker.btn.point.enforce === true) {
        const setData = this.$util.setPoint(this.tData.behavior.masker.btn.point.set);
        if(setData.length === 3) { //传入三个值才会设置
          (<any>window)._uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
        } else {
          (<any>window)._uxt.push(['_trackEvent', `${this.tData.title}_${(<any>window).channel}_答题页`, '点击', '重新开始']); 
        }
      }
      this.$store.commit('Timing', this.tData.behavior.clock.outSet); //初始化计时器
      this.$store.commit('scoring', 0); //初始化计分器
      this.$router.go(0); //刷新一波当前页面
    }
  },
  created: function() {
    if(this.clock) {
      this.$store.commit('Timing', this.tData.behavior.clock.outSet); //刷新时钟，避免前进后退的时候时钟组件不刷新
      this.$store.commit('scoring', 0);
    }
    if(this.tData.behavior.bg) {
      this.$store.commit('setAppBg', this.tData.behavior.bg.url);
      this.$store.commit('setAppBgStyle', this.tData.behavior.bg.style);
    }
    document.title = this.tData.behavior.title || this.tData.title;
    this.$store.commit('refreshTopic', 0); //获取题目
  },    
  mounted: function() {
    if(this.tData.behavior.point.enforce === false) {
      const setData = this.$util.setPoint(this.tData.behavior.btn.point.set);
      if(setData.length === 3) { //传入三个值才会设置
        (<any>window)._uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
      } else {
        (<any>window)._uxt.push(['_trackEvent', `${this.tData.title}_${(<any>window).channel}_答题页`, '进入页面', '访问答题页']); 
      }
    }
  },
})
